import React from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import Chart from "./Chart";
import { makeTimeText } from "../utils/utils";

export default function Result() {
  const result = useSelector((state) => state.result);
  const { time, correctCount, inCorrectCount } = result;

  return (
    <ResultWrapper>
      <h2 className="result-comment"> </h2>
      <ResultContainer>
        <ResultTextContainer>
          <ResultText> : {makeTimeText(time)}</ResultText>
          <ResultText> : {correctCount + inCorrectCount}</ResultText>
          <ResultText> : {correctCount}</ResultText>
          <ResultText> : {inCorrectCount}</ResultText>
        </ResultTextContainer>
        <Chart data={result}/>
      </ResultContainer>
    </ResultWrapper>
  );
}

const ResultWrapper = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

  .result-comment {
    margin-top: 25px;
    font-size: 30px;
  }
`;

const ResultContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
`;

const ResultTextContainer = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
`;

const ResultText = styled.div`
  font-size: 20px;
  font-weight: bold;
`;
